.CartBtn {
  display: flex;
  align-items: center;

  &__price {
    display: block;
    padding-right: 13px;
    height: 25px;
    line-height: 25px;
    border-right: 1px solid rgba(255, 255, 255, 0.25);
  }

  &__icon  {
    margin-left: 13px;
  }

  &__count {
    margin-left: 8px;
  }
}